import React, {useEffect ,useState} from 'react';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Fade from '@mui/material/Fade';
import IconButton from '@mui/material/IconButton';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Box from '@mui/material/Box';
import {Modal} from '@mui/material';
import {DELET_SALEPOINT} from 'query/deletQuerys/deletQuerys';
import PropTypes from 'prop-types';
import {useLazyQuery} from '@apollo/client';
import EditSellingpoint from '../EditSellingpoint/EditSellingpoint';
const SellingpointActions = ({sellpoint}) => {

  const [deletSalePoint, {data}] = useLazyQuery(DELET_SALEPOINT);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [openEditEmployeeModal, setOpenEditEmployeeModal] = useState(false);

  const handleCloseVariantModal = () => {
    setOpenEditEmployeeModal(false);
  };
  useEffect(() => {
    if (data) {
      console.log(data);
      alert(data.deletSalePoint.result);
    }
  }, [data]);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleOpenEdit = () => {
    setOpenEditEmployeeModal(true);
  };

  const hanleDelet = (e) => {
    e.preventDefault();
    deletSalePoint({
      variables: {id: sellpoint.id, email: sellpoint.email},
    });
  };

  return (
    <>
    <Box>
      <IconButton
        aria-controls='alpha-menu'
        aria-haspopup='true'
        onClick={handleClick}
      >
        <MoreVertIcon />
      </IconButton>
      <Menu
       id='alpha-menu'
       anchorEl={anchorEl}
       keepMounted
       open={open}
       onClose={handleClose}
       TransitionComponent={Fade}
      >
        <MenuItem style={{fontSize: 14}} onClick={handleClose}>
          View Empployee
        </MenuItem>
        <MenuItem style={{fontSize: 14}} onClick={handleOpenEdit}>
          Edit
        </MenuItem>
        <MenuItem style={{fontSize: 14}} onClick={(e) => hanleDelet(e)}>
          Delete
        </MenuItem>
      </Menu>
    </Box>
    <Modal
        aria-labelledby='transition-modal-title'
        aria-describedby='transition-modal-description'
        open={openEditEmployeeModal}
        onClose={handleCloseVariantModal}
        closeAfterTransition
      >
        <EditSellingpoint data={sellpoint} />
      </Modal>
    </>
  );
};
SellingpointActions.propTypes = {
  sellpoint: PropTypes.object.isRequired,
};

export default SellingpointActions;
