import React, {useEffect, useState} from 'react';
import SellingpointTable from './SellingpointTable';
import AppsContainer from '../../../@crema/core/AppsContainer';
import {useIntl} from 'react-intl';
import {useDispatch, useSelector} from 'react-redux';
import {getSalePoint} from '../../../redux/actions';
import {Button, Hidden} from '@mui/material';
import AppsHeader from '../../../@crema/core/AppsContainer/AppsHeader';
import AppsContent from '../../../@crema/core/AppsContainer/AppsContent';
import AppsPagination from '../../../@crema/core/AppsPagination';
import Box from '@mui/material/Box';
import AppInfoView from '../../../@crema/core/AppInfoView';
import AppSearchBar from '../../../@crema/core/AppSearchBar';
import {useNavigate} from 'react-router-dom';

const Sellingpoint = () => {
  const {messages} = useIntl();
  const dispatch = useDispatch();
  const salePoint = useSelector(({ecommerce}) => ecommerce.salePoint);
  const salePointCount = useSelector(({ecommerce}) => ecommerce.salePointCount);
  const lastVisible = useSelector(({ecommerce}) => ecommerce.lastVisible);
  const [index, setIndex] = useState('');
  const navigation = useNavigate();
  const [isNext, setIsNext] = useState();

  const [page, setPage] = useState(0);
  const [search, setSearchQuery] = useState('');
  const addSellingpoint = () => {
    navigation('/management/Sellingpoint/AddSellingpoint');
  };
  const onPageChange = (event, value) => {
    if (value > page) {
      setIsNext(true);
    } else {
      setIsNext(false);
    }
    setIndex(lastVisible);

    setPage(value);
  };
  useEffect(() => {
    dispatch(getSalePoint(search, page, index, isNext));
  }, [dispatch, search, page, index, isNext]);

  const onSearchCustomer = (e) => {
    setSearchQuery(e);
    setPage(0);
  };

  return (
    <>
      <AppsContainer title={'Selling points'} fullView>
        <AppsHeader>
          <Box
            sx={{
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
              width: 1,
            }}
          >
            <AppSearchBar
              iconPosition='right'
              overlap={false}
              onChange={(event) => onSearchCustomer(event.target.value)}
              placeholder={messages['common.searchHere']}
            />
            <Box
              sx={{
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                ml: 'auto',
              }}
            >
              <Button
                variant='contained'
                color='primary'
                onClick={addSellingpoint}
              >
               {messages['AddSellingpoint']}
              </Button>

              <Hidden smDown>
                <AppsPagination
                             rowsPerPage={40}

                  count={salePointCount}
                  page={page}
                  onPageChange={onPageChange}
                />
              </Hidden>
            </Box>
          </Box>
        </AppsHeader>

        <AppsContent
          sx={{
            paddingTop: 2.5,
            paddingBottom: 2.5,
          }}
        >
          <SellingpointTable salePoint={salePoint} />
        </AppsContent>

        <Hidden smUp>
          <AppsPagination
                       rowsPerPage={40}

            count={salePointCount}
            page={page}
            onPageChange={onPageChange}
          />
        </Hidden>
      </AppsContainer>

      <AppInfoView />
    </>
  );
};

export default Sellingpoint;
