import React, { useEffect, useState} from 'react';
import {Card, Button, Grid, Divider} from '@mui/material';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import CodeIcon from '@mui/icons-material/Code';
import PropTypes from 'prop-types';
import IconButton from '@mui/material/IconButton';
import Box from '@mui/material/Box';
import AppAnimate from '../../../../@crema/core/AppAnimate';
import TextField from '@mui/material/TextField';
import {Fonts} from '../../../../shared/constants/AppEnums';


import {useIntl} from 'react-intl';
import { EDIT_SALEPOINT } from 'query/deletQuerys/deletQuerys';
import { useLazyQuery } from '@apollo/client';

const EditSellingpoint = ({
  // title,
  data,
  description,
  source,
}) => {

  const [EditSalepoint, {data:result}] = useLazyQuery(EDIT_SALEPOINT);

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    bgcolor: 'background.paper',
    boxShadow: 24,
  };
  const [formData, updateFormData] = useState();
  const [viewSource, setToggleViewSource] = useState(false);
  const [animation, setAnimation] = useState(false);
  const {messages} = useIntl();

  useEffect(() => {
    if (result) {
      console.log(result);
      // alert(data.deletSalePoint.result);
    }
  }, [result]);
  const handleChange = (e) => {
    updateFormData({
      ...formData,

      [e.target.name]: e.target.value.trim(),
    });
  };

  const EditSPoint = (e) => {
    e.preventDefault();
    var today = new Date();
    var dd = String(today.getDate()).padStart(2, '0');
    var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    var yyyy = today.getFullYear();
    today = mm + '/' + dd + '/' + yyyy;
    const form = {
      id: data.id+"",
      name: formData.name?formData.name:data.name,
      phone_Number: formData.Phone_Number?formData.Phone_Number:data.phone_Number,
      email: data.email,
      address: formData.address?formData.address:data.address,
      password: formData.password?formData.password:data.password,
      time:data.time,
      addDate: data.addDate,
      Role: 'salePoint',
      
    };
    console.log(form);
    EditSalepoint({
      variables: {data: form, oldPasswar: data.password},
    });
    // EditSalepoint(form, data.password);
  };

  return (
    <form style={style} onSubmit={(e) => EditSPoint(e)}>
      <AppAnimate animation='transition.slideUpIn' delay={200}>
        <Card>
          <CardHeader
            sx={{
              py: 4,

              pb: 1,
              px: 5,
              display: 'flex',
              alignItems: 'center',
              minHeight: 50,
              boxSizing: 'border-box',
              '& .MuiTypography-h5': {
                fontSize: 14,
                fontWeight: Fonts.BOLD,
                marginBottom: 0.25,
              },
            }}
            // title={title}
            subheader={description}
            root={{
              subheader: {
                fontSize: 13,
              },
            }}
            action={
              source ? (
                <Box>
                  <IconButton
                    aria-label='view code'
                    onClick={() => {
                      if (animation) {
                        setAnimation(!animation);
                        setTimeout(() => setToggleViewSource(!viewSource), 400);
                      } else {
                        setAnimation(!animation);
                        setToggleViewSource(!viewSource);
                      }
                    }}
                    size='large'
                  >
                    <CodeIcon />
                  </IconButton>
                </Box>
              ) : null
            }
          />

          <CardContent sx={{px: 10, pt: 1}}>
            <Grid container item xs={12} sm={5} spacing={4} textAlign={'start'}>
              <Grid item textAlign={'start'} xs={12} lg={6}>
                <TextField
                  id='outlined-basic'
                  name='name'
                  label={['Full Name']}
                  variant='outlined'
                  defaultValue={data.name}
                  onChange={handleChange}
                  required
                />
              </Grid>

              <Grid item textAlign={'start'} xs={12} lg={6}>
                <TextField
                  type='number'
                  id='Phone_Number'
                  name='Phone_Number'
                  label={['Phone Number']}
                  variant='outlined'
                  defaultValue={data.phone_Number}
                  onChange={handleChange}
                  required
                />
              </Grid>
              <Grid item textAlign={'start'} xs={12} lg={6}>
                <TextField
                  id='email'
                  name='email'
                  label={['Email']}
                  variant='outlined'
                  onChange={handleChange}
                  disabled={true}
                  defaultValue={data.email}
                  required
                />
              </Grid>
              <Grid item textAlign={'start'} xs={12} lg={6}>
                <TextField
                  id='password'
                  name='password'
                  label={['Password']}
                  type='password'
                  variant='outlined'
                  defaultValue={data.password}
                  onChange={handleChange}
                  required
                />
              </Grid>

              <Grid item xs={12} style={{minHeight: '100px'}}>
                <TextField
                  style={{width: '100%'}}
                  id='address'
                  name='address'
                  label={['Address']}
                  variant='outlined'
                  size='large'
                  inputProps={{style: {minHeight: 70}}}
                  onChange={handleChange}
                  defaultValue={data.address}
                  required
                />
              </Grid>

              <Grid item sx={0} sm={1}>
                <Divider
                  orientation='horizontal'
                  style={{
                    marginRight: '50%',
                    marginLeft: '50%',
                    height: '100%',
                    width: '2px',
                  }}
                />
              </Grid>

              <Grid container xs={12} sm={6} item spacing={2}>
                <Grid
                  item
                  maxHeight={{xs: '170px', lg: '250px'}}
                  overflow={'auto'}
                  xs={12}
                ></Grid>
              </Grid>
            </Grid>
            <Button type='submit' variant='contained' color='primary'>
              {messages['sidebar.mangments.EditSellingpoint']}
            </Button>
          </CardContent>
        </Card>
      </AppAnimate>
    </form>
  );
};

EditSellingpoint .propTypes = {
  source: PropTypes.any,
  data : PropTypes.object,
  // title: PropTypes.node.isRequired,
  description: PropTypes.node,
  // buttonCompMethod:  PropTypes.any.isRequired,
};

export default EditSellingpoint ;
